<template>
	<div class="groupchat-container">
		<div class="groupchat-header">
			<div class="groupchat-zhong">
				<div class="groupchat-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="groupchat-ti">群聊</div>
				<div></div>
			</div>
		</div>
		<div class="groupchat-content">
			<div ref="$refs.chatcontheight" class="groupchat-list">
				<span v-for="(item,index) in message_list" :key="index">
					<!--<div class="groupchat-flex" v-if="item.type==0">
						<div class="groupchat-touxiang">
							<img src="../assets/icon_touxiang1.png">
						</div>
						<div id="box">
							<div class="groupchat-wen" v-if=' item.imgpatha == 0'>{{item.data}}</div>
						</div>
					</div>-->
					<div class="groupchat-reverse" v-if="item.type==1">
						<div class="groupchat-touxiang">
							<img src="../assets/icon_touxiang2.png">
						</div>
						<div id="box2">
							<div class="groupchat-wen" v-if=' item.imgpatha == 1'>{{item.data}}</div>
							<!--<img class="groupchat-fas" v-if=" item.imgpatha != 1 "  :src="Imgpath+item.imgpath" />-->
							<yd-lightbox close-text num>	
									<yd-lightbox-img v-if=" item.imgpatha != 1 " :src="Imgpath+item.imgpath"></yd-lightbox-img>
							</yd-lightbox>
						</div>
					</div>
					<div v-else>
						<div class="niming">匿名</div>
						<div class="groupchat-flex">
							<div class="groupchat-touxiang">
								<img src="../assets/toxiang-niming@2x.png">
							</div>
							<div id="box">
								<div class="groupchat-wen" v-if=' item.imgpatha == 1'>{{item.data}}</div>
								<!--<img class="groupchat-fas" v-if=" item.imgpatha != 1 " :src="Imgpath+item.imgpath" />-->
								<yd-lightbox close-text num>	
										<yd-lightbox-img v-if=" item.imgpatha != 1 " :src="Imgpath+item.imgpath"></yd-lightbox-img>
								</yd-lightbox>
								<!--<span v-else ></span>-->
							</div>
						</div>
					</div>
					<!--<mu-container>
					  <mu-dialog width="100%" :open.sync="openSimple">
					  	<div class="showtu-lun">
					  		<mu-carousel interval='80000' @change="change()">
								<mu-carousel-item v-for="(item,index) in message_list" :key="index">
									<img :src="Imgpath+item.atlas_pic" @click="openBotttomSheet(item)">
								</mu-carousel-item>
							</mu-carousel>
					  	</div>
					  </mu-dialog>
					</mu-container>-->
				</span>
			</div>
		</div>
		<div class="groupchat-footer">
			<div class="groupchat-tj" @click="iMsg()">
				<img src="../assets/tianjia-qunliao.png">
			</div>
			<!--<div class="groupchat-tj">
					<img src="../assets/icon_bq.png">
				</div>-->
			<div class="groupchat-sousuo">
				<input type="text" v-model="message">
			</div>
			<div class="groupchat-fasong" @click="sendclick()">发送</div>
		</div>
	</div>
</template>

<script>
	import Qs from "qs";
//	import Vue from 'vue';
//	import {LightBox, LightBoxImg, LightBoxTxt} from 'vue-ydui/dist/lib.rem/lightbox';
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				ws: null,
				user_info: {},
				message: '',
				message_list: [],
				headImg: '',
				openSimple: false,
				active:'',
				img_url: '',
			}
		},
		destroyed: function() {
			this.ws.close();
		},
		methods: {
			back() {
				window.history.back();
				var message="close:true"
				this.ws.send(message);
			},
			//			安卓
			iMsg() {
				var that = this
				dsBridge.call('uploadImg', that.user_info.user_id);
				dsBridge.register('addImgCallback', function(r) {
					that.ws.send("chatpic" + r);

				})
			},
			openSimpleDialog (item,index) {
//				this.active = index
				let that = this
				that.img_url = item.atlas_pic;
				that.openSimple = true;
//				setTimeout(function(){ 
//					that.openSimple = true;
//				}, 1000);
				
		    },
		    closeSimpleDialog () {
		      this.openSimple = false;
		      this.closeBottomSheet()
		    },
			chats() {
				var that = this;

				that.$http.post('/chat/chats', Qs.stringify({}), {
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					}
				}).then((res) => {
					//					console.log(res)
					res.data.forEach(function(e) {
						//						console.log(e)
						//						console.log(that.user_info.user_id)
						var str = e.chat_content;
						//						console.log(str);
						if(str.indexOf("chatpic") != -1) {
							//							console.log(str.indexOf("chatpic/") != -1)
							if(e.chat_user_id == that.user_info.user_id) {
								// 自己发送的消息
								that.message_list.push({
									type: 1,
//									data: '用户[' + e.user.user_nickname + ']：',
									imgpath: e.chat_content.split("chatpic")[1]
								})
							} else {
								that.message_list.push({
									type: 2,
//									data: '用户[' + e.user.user_nickname + ']：',
									imgpath: e.chat_content.split("chatpic")[1]
								})
							}

						} else {
							if(e.chat_user_id == that.user_info.user_id) {
								// 自己发送的消息
								that.message_list.push({
									type: 1,
//									data: '用户[' + e.user.user_nickname + ']：' + e.chat_content,
									data: e.chat_content,
									imgpatha: 1
								})
							} else {
								that.message_list.push({
									type: 2,
//									data: '用户[' + e.user.user_nickname + ']：' + e.chat_content,
									data: e.chat_content,
									imgpatha: 1
								})
							}
						}
//												console.log(that.message_list)

					})
					that.scroll();
				})

			},

			wsCreate() {
				var that = this;
				//				console.log(that.user_info.user_id)
				var url = this.ws_api + that.user_info.user_id;
				this.ws = new WebSocket(url);
				this.ws.onopen = function() {
					console.log("建立 websocket 连接...");
				};
				this.ws.onmessage = function(event) {
					//服务端发送的消息
					var _type = event.data.split('[')[0];
					var _user = event.data.split('[')[1].split(']')[0];
					var _msg = event.data.split('[')[1].split(':')[1];

					// console.log(_type);
					// console.log(_user);
					 console.log(_msg);
					//					console.log(event)

					if(_type == '欢迎用户') {
						// 系统消息
						that.message_list.push({
							type: 0,
							data: event.data,
							imgpatha: 0,
						})
					} else {
						var str = event.data;
//						console.log(str);
						if(str.indexOf("chatpicupload/") != -1) {
							if(_type == '用户' && _user == that.user_info.user_nickname) {
//								alert(1)
								// 自己发送的消息
								that.message_list.push({
									type: 1,
									data:_msg,
//									data: _msg,
									imgpatha: 2,
									imgpath: _msg.split("chatpic")[1]
								})
							} else {
//								alert(2)
								// 其他消息
								that.message_list.push({
									type: 2,
									data: _msg,
//									data: _msg,
									imgpatha: 2,
									imgpath:_msg.split("chatpic")[1]
								})	
							}

						} else {
							if(_type == '用户' && _user == that.user_info.user_nickname) {
//								alert(3)
								// 自己发送的消息
								that.message_list.push({
									type: 1,
//									data: event.data,
									data: _msg,
									imgpatha: 1,
//									imgpath: _msg
								})
							} else{
//								alert(4)
								// 其他消息
								that.message_list.push({
									type: 2,
//									data: event.data,
									data: _msg,
									imgpatha: 1,
									imgpath:_msg
//									imgpath: _msg
								})
							}
						}

					}
					that.scroll();
				};
				this.ws.onclose = function() {
					console.log("关闭 websocket 连接...");
				}
			},
			// scrollToBottom() {
			// 	this.$nextTick(() => {
			// 		var container = this.$el.querySelector(" 需要滚动条处于底部的容器 ");
			// 		container.scrollTop = container.scrollHeight;
			// 	})
			// },
			// _initScroll() {
			// 	this.chartScroll = new BScroll(this.$refs.chatcontheight, {
			// 		click: true
			// 	})
			// },
			scroll: function() {
				setTimeout(function() {
					var _height = 1;
					var w_height = window.getComputedStyle(document.querySelector('.groupchat-list')).height;
					if(!!_height) {
						_height = w_height.split('px')[0];
					} else {

					}
					document.querySelector('.groupchat-content').scrollTop = _height;
					// console.log(window.getComputedStyle(document.querySelector('.groupchat-list')).height);
				}, 222)
			},

			sendclick() {
				if(this.message == ''){
//					this.$alert('输入不能为空','提示');
					return;
				}else{
					
				}
				this.ws.send(this.message);
				this.message = '';
			}
		},
		created() {
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
				//				console.log(this.user_info)
			}
			this.chats();
			this.wsCreate();
		},
		mounted() {
			// this.scroll();
			//			页面刚加载完 dom 也加载完 的时候执行
			// this.scrollToBottom();
			// this.$nextTick(() => {

			// this._initScroll();
			// let allLi = this.$refs.chatcontheight.querySelectorAll("li");
			// this.chartScroll.scrollToElement(allLi[allLi.length - 1], 0);
			// });
		},
		//每次页面渲染完之后滚动条在最底部
		updated: function() {
			// this.scroll();
			// console.log('update');
			// this.$nextTick(() => {
			// console.log(document.querySelector('.groupchat-list').style.height);
			// 	this._initScroll();
			// 	let allLi = this.$refs.chatcontheight.querySelectorAll("li");
			// 	this.chartScroll.scrollToElement(allLi[allLi.length - 1], 0);
			// });
		}
	}
</script>

<style scoped>
	/*#box2{
		display: flex;
		justify-content: center;
	}*/
	#box2 img {
		/*width: 90%;
		height: auto;*/
		width: 5rem;
		height: 5rem;
		/*margin: 0 auto;*/
		margin-left: 1rem;
	}
	
	#box img {
		width: 5rem;
		height: 5rem;
	}
	
	.groupchat-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.groupchat-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.groupchat-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.groupchat-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.groupchat-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.groupchat-content {
		width: 100%;
		height: calc(100% - 105px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.groupchat-flex {
		display: flex;
		margin-top: 2rem;
		/*position: relative;
		bottom: 0px;*/
	}
	
	.groupchat-touxiang img {
		width: auto;
		height: 3rem;
		margin-left: 1.5rem;
		margin-right: 1rem;
		/*margin-top: 1.5rem;*/
	}
	
	.groupchat-qipao img {
		width: auto;
		height: 2.5rem;
		margin-top: 0.3rem;
	}
	
	.groupchat-hua {
		font-size: 1.4rem;
		color: #333333;
		position: relative;
		left: -11rem;
		top: 0.7rem;
	}
	
	.groupchat-reverse {
		display: flex;
		flex-direction: row-reverse;
		margin-top: 2rem;
		/*position: relative;
		bottom: 0;*/
	}
	
	.groupchat-hui {
		font-size: 1.4rem;
		color: #333333;
		position: relative;
		right: -7.2rem;
		top: 0.7rem;
		/*margin: 2rem;*/
	}
	
	.groupchat-footer {
		height: 55px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #fff;
		border-top: 1px solid #f0f0f0;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
	
	.groupchat-tj img {
		width: auto;
		height: 2rem;
	}
	
	.groupchat-sousuo {
		border: 1px solid #f53981;
		border-radius: 20px;
		background: #fff;
		width: 60%;
		height: 2.5rem;
	}
	
	.groupchat-sousuo input {
		font-size: 1.5rem;
		height: 2rem;
		padding: 0.2rem 0;
		line-height: 2.5rem;
		color: #9fa0a0;
		border: none;
		outline: none;
		background: #fff;
		margin-left: 0.8rem;
		margin-top: 0.2rem;
		width: 90%;
	}
	
	.groupchat-fasong {
		font-size: 1.5rem;
		color: #f53981;
		border: 1px solid #f53981;
		background: #fff;
		border-radius: 10px;
		padding: 0.2rem 1.3rem;
	}
	
	#box {
		/*position: absolute;*/
		top: 72px;
		left: 70px;
		background: #fff;
		-moz-border-radius: 12px;
		-webkit-border-radius: 12px;
		border-radius: 12px;
		word-break: break-all;
		max-width: 70%;
		color: #f53981;
		border: 1px solid #f53981;
	}
	
	#box:before {
		position: absolute;
		/*content: "";*/
		width: 0;
		height: 0;
		right: 75%;
		/*top: 38px;*/
		border-top: 13px solid transparent;
		border-right: 26px solid #FFFFFF;
		border-bottom: 13px solid transparent;
		/*border-width: 10rem;*/
	}
	
	#box2 {
		/*	position: absolute;*/
		top: 135px;
		right: 70px;
		background: #fff;
		-moz-border-radius: 12px;
		-webkit-border-radius: 12px;
		border-radius: 12px;
		word-break: break-all;
		max-width: 70%;
	}
	
	#box2:before {
		position: absolute;
		/*content: "";*/
		width: 0;
		height: 0;
		left: 75%;
		/*top: 38px;*/
		border-top: 13px solid transparent;
		border-left: 26px solid #8cadf5;
		border-bottom: 13px solid transparent;
	}
	
	.groupchat-wen {
		/*position: relative;
		top: 0;*/
		font-size: 1.5rem;
		margin: 0.5rem;
		
	}
	.groupchat-fas{
		width: 5rem;
		height: 5rem;
	}
	textarea {
		width: 100%;
		height: calc(100% - 5px);
		background: #EAEAEA;
		border: none;
	}
	
	::-webkit-scrollbar {
		width: 5px;
		height: 5px;
	}
	
	::-webkit-scrollbar-track-piece {
		background-color: rgba(0, 0, 0, 0.2);
		-webkit-border-radius: 6px;
	}
	
	::-webkit-scrollbar-thumb:vertical {
		height: 5px;
		background-color: rgba(125, 125, 125, 0.7);
		-webkit-border-radius: 6px;
	}
	
	::-webkit-scrollbar-thumb:horizontal {
		width: 5px;
		background-color: rgba(125, 125, 125, 0.7);
		-webkit-border-radius: 6px;
	}
	.niming{
		font-size: 0.6rem;
		color: #BFBFBF;
		position: relative;
		margin-bottom: -1.5rem;
		padding-left: 2rem;
	}
	/*轮播图*/
	.showtu-lun{
		width: 100%;
		height: 100%;
		position: relative;
	}
	.showtu-lun img {
		width: 100%;
		/*height: 100%;*/
		height: auto;
		margin: 0 auto;
	}

	/deep/ .mu-tab {
		min-width: 0;
	}
	
	/*/deep/ .mu-carousel-indicator-icon {
		background: #fe4563;
	}*/
	
	/deep/ .mu-carousel-button.mu-icon-button {
		font-size: 0;
		z-index: 0;
	}
	/deep/ .mu-carousel {
		width: 100%;
		height: 75rem;
	}
</style>


